import React, { useState, useEffect } from "react";
import MenubarAdmin from "../../../layouts/MenubarAdmin";

//functions
import {
  readCategory,
  editCategory,
} from "../../../functions/category";
import { useParams, useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import { useSelector } from 'react-redux';

const UpdateCategory = () => {

  const { user} = useSelector((state) => ({...state}));

  const navigate = useNavigate();
  const param = useParams();

  const [name, setName] = useState('');

  useEffect(() => {
    loadData(user.token, param.id);
  }, [user.token, param.id])

  const loadData = (authtoken,id) => {
    readCategory(authtoken,id)
      .then((response) => {
        setName(response.data.name)
      }).catch((error) => {
        console.log(error)
      });
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    editCategory(user.token, param.id, { name })
      .then((response) => {
        navigate("/admin/create-category");
        toast.success(`Update ${response.data.name} to ${name} Success!`)
      }).catch((error) => {
        console.log(error)
      })
  }

  return (
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarAdmin />
        </div>

        <div className="col">
          <h1>Update Category</h1>
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label>แก้ไขหมวดหมู่สินค้า</label>
              <input type="text"
                name="name"
                className="form-control"
                value={name}
                autoFocus
                required
                onChange={(e) => setName(e.target.value)}
              />
              <button className="btn btn-outline-primary">แก้ไขหมวดหมู่สินค้า</button>
            </div>
          </form>
        </div>

      </div>
    </div>
  )
}

export default UpdateCategory