import React, { useState, useEffect } from 'react'
import { emptyCart, getUserCart, saveAddress, saveOrder } from '../functions/users';
import { useDispatch, useSelector } from 'react-redux';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

const CheckOut = () => {
  const { user } = useSelector((state) => ({ ...state }));
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [products, setProducts] = useState([]);
  const [total, setTotal] = useState(0);
  const [address, setAddress] = useState('');
  const [addressSaved, setAddressSaved] = useState(false);

  useEffect(() => {
    getUserCart(user.token)
      .then(response => {
        console.log(response.data)
        setProducts(response.data.products)
        setTotal(response.data.cartTotal)
      }).catch(error => {
        console.log(error)
      })
  }, [])

  const handleSaveAddress = () => {

    saveAddress(user.token, address)
      .then(response => {
        console.log(response)
        if (response.data.ok === true) {
          toast.success('Address Saved')
          setAddressSaved(true)
        }
      }).catch(error => {
        console.log(error)
      })
    console.log(address)
  }

  const handleCreateOrder = () => {
    saveOrder(user.token)
      .then(response => {
        console.log(response.data)

        // clear db
        emptyCart(user.token)

        // clear store
        dispatch({
          type: 'ADD_TO_CART',
          payload: []
        })

        // clear localStorage
        if (typeof window !== 'undefined') {
          localStorage.removeItem('cart')
        }

        toast.success('Clear Order Success')

        navigate('/user/history');
      })
  }

  return (
    <div className='container-fluid'>
      <div className='row'>
        <div className='col-md-6'>
          <h4>Address</h4>
          <br />
          text area
          <ReactQuill value={address} onChange={setAddress} />
          <button onClick={handleSaveAddress}>Save Address</button>
        </div>
        <div className='col-md-6'>
          <h4>order summary</h4>
          <hr />
          <p>Product {products.length}</p>
          <hr />
          <p>List Product</p>
          {
            products.map((item, index) =>
              <div key={index}>
                <p>
                  {item.product.title} x {item.count} = {item.price * item.count}
                </p>
              </div>
            )
          }
          <hr />
          Total : <strong>{total}</strong>
          <br />
          <button
            disabled={!addressSaved || !products.length}
            className='btn btn-primary mt-3'
            onClick={handleCreateOrder}
          >
            Check Out
          </button>
        </div>
      </div>
    </div>
  )
}

export default CheckOut