import React, { useEffect, useState } from 'react'
import MenubarAdmin from "../../layouts/MenubarAdmin";
import { useSelector } from 'react-redux';
// function
import { getOrders } from '../../functions/users';
import { getOrdersAdmin, updateStatusOrder } from '../../functions/admin';
// notify
import { toast } from 'react-toastify';

const Orders = () => {

  const { user } = useSelector((state) => ({ ...state }))
  const [orders, setOrders] = useState([]);
  useEffect(() => {
    loadData()
  }, []);

  const loadData = () => {
    getOrdersAdmin(user.token)
      .then(response => {
        setOrders(response.data)
      }).catch(error => {
        console.log(error)
      })
  }

  console.log(orders)

  const handleChangeStatus = (orderId,orderstatus) => {

    updateStatusOrder(user.token, orderId , orderstatus)
    .then(response=>{
      console.log(response.data)
      toast.info(response.data.orderstatus+' sucess')
      loadData()
    }).catch(error=>{
      console.log(error)
    })
  }

  return (
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarAdmin />
        </div>

        <div className="col">
          <h1>ORDERS</h1>

          {
            orders.map((item, index) => {
              return <div key={index}>
                <h2>Order {' ' + item.orderstatus}</h2>
                Orderd By : {item.orderdBy.username}
                <select 
                value={item.orderstatus}
                onChange={(e) => handleChangeStatus(item._id,e.target.value)}
                style={{width:'200px',alignSelf:'center'}}
                className='form form-select'>
                  <option value="Not Process">Not Process</option>
                  <option value="Processing">Processing</option>
                  <option value="Calcelled">Cancelled</option>
                  <option value="Completed">Completed</option>
                </select>
                <table className="table">
                  <thead>
                    <tr>
                      <th>Title</th>
                      <th>Price</th>
                      <th>Count</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      item.products.map((p, i) => (
                        <tr key={i}>
                          <td>{p.product.title}</td>
                          <td>{p.price}</td>
                          <td>{p.count}</td>
                        </tr>
                      ))
                    }
                    <tr>
                      <td colSpan={3}>ราคาสุทธิ: <strong><u>{item.cartTotal}</u></strong></td>
                    </tr>
                  </tbody>
                </table>

              </div>
            })
          }


        </div>

      </div>
    </div>
  )
}

export default Orders