import React from 'react'
import { Card } from 'antd';
import { HeartOutlined, ShoppingCartOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import { useDispatch, useSelector } from 'react-redux';

// lodash
import _ from 'lodash';

// function
import { addToWishList } from '../functions/users';

import { toast } from 'react-toastify';

// const { Meta } = Card;

const SingleProductCard = ({ product }) => {

  const dispatch = useDispatch();
  const { user } = useSelector((state)=>({...state}))

  const { _id, title, description, images, price, quantity, sold, category } = product;

  const handleAddToCart = () => {
    let cart = []
    if(localStorage.getItem('cart')){
      cart = JSON.parse(localStorage.getItem('cart'))
    }
    cart.push({
      ...product,
      count:1
    })

    let unique = _.uniqWith(cart,_.isEqual)

    localStorage.setItem('cart',JSON.stringify(unique))

    dispatch({
      type: "ADD_TO_CART",
      payload: unique
    })

    dispatch({
      type: "SET_VISIBLE",
      payload: true
    })
  }

  const handleAddToWishList = (e) => {
    
    if(user){
      addToWishList(user.token,_id)
      .then(response=>{
        console.log(response.data)
        toast.success('Add to wishlist success')
      })
      .catch(error=>{
        console.log(error)
      })
    }else{
      toast.error('Login Please')
    }
    
  }

  return (
    <>
      <div className='col-md-7'>
        <Carousel autoPlay showArrows={true} infiniteLoop>
          {images && images.map((item) => <img src={item.url} key={item.public_id} alt="" />)}
        </Carousel>

        <ul className="nav nav-tabs" id="myTab" role="tablist">
          <li className="nav-item" role="presentation">
            <button className="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home-tab-pane" type="button" role="tab" aria-controls="home-tab-pane" aria-selected="true">Home</button>
          </li>
          <li className="nav-item" role="presentation">
            <button className="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile-tab-pane" type="button" role="tab" aria-controls="profile-tab-pane" aria-selected="false">Profile</button>
          </li>
          <li className="nav-item" role="presentation">
            <button className="nav-link" id="contact-tab" data-bs-toggle="tab" data-bs-target="#contact-tab-pane" type="button" role="tab" aria-controls="contact-tab-pane" aria-selected="false">Contact</button>
          </li>
        </ul>
        <div className="tab-content" id="myTabContent">
          <div className="tab-pane fade show active" id="home-tab-pane" role="tabpanel" aria-labelledby="home-tab" tabIndex="0">home</div>
          <div className="tab-pane fade" id="profile-tab-pane" role="tabpanel" aria-labelledby="profile-tab" tabIndex="0">profile</div>
          <div className="tab-pane fade" id="contact-tab-pane" role="tabpanel" aria-labelledby="contact-tab" tabIndex="0">contact</div>
        </div>

      </div>
      <div className='col-md-5'>
        <h1 className='bg-info p-3'>{title}</h1>
        <Card
          key={product._id}
          className='mb-3 img-fluid'
          actions={[
            <a onClick={handleAddToWishList}>
              <HeartOutlined className='text-info' /> 
              <br />
              Add to wish list
            </a>,
            <>
              <a onClick={handleAddToCart}>
                <ShoppingCartOutlined className='text-danger' />
                <br/>
                Add to cart
              </a>
            </>,
          ]}
        >
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              Price <span className='float-end'>{price}</span>
            </li>
            <li className="list-group-item">
              Quantity <span className='float-end'>{quantity}</span>
            </li>
            <li className="list-group-item">
              Sold <span className='float-end'>{sold}</span>
            </li>
            { category && 
            <li className="list-group-item">
              Category <span className='float-end'>{category.name}</span>
            </li>
            }
          </ul>
          {description}
        </Card>
      </div>
    </>
  )
}

export default SingleProductCard