import React, { useState, useEffect } from "react";
import MenubarAdmin from '../../../layouts/MenubarAdmin';
import { toast } from "react-toastify";
import { useSelector } from "react-redux";

// functions
import { createProduct } from "../../../functions/product";
import { listCategory } from "../../../functions/category";
import FileUpload from "./FileUpload";
import { Spin } from 'antd';

const initialstate = {
  title: "Notebook",
  description: "DESS",
  categories: [],
  category: "",
  price: "200",
  quantity: "5",
  images: []
}

const CreateProduct = () => {

  const { user } = useSelector((state) => ({ ...state }))
  const [values, setValues] = useState(initialstate);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadData(user.token);
  }, [user.token])

  const loadData = (authtoken) => {
    listCategory(authtoken)
      .then(response => {
        console.log(response.data)
        setValues({ ...values, categories: response.data })
      }).catch(error => {
        console.log(error)
      })
  }

  console.log('values', values)

  const handleChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value })
    // console.log(values)
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    createProduct(user.token, values)
      .then((response) => {
        console.log(response)
        toast.success(`Insert Data ${response.data.title} Success`)
        window.location.reload();
      }).catch((error) => {
        console.log(error.response)
        toast.error(error.response.data)
      });
    // console.log(values)
  }

  return (
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarAdmin />
        </div>

        <div className="col">
          {
            loading
            ? <h1>Loading...<Spin /></h1> //true
            : <h1>Create Product Page</h1> //false
          }

          <form onSubmit={handleSubmit} className="col-md-4">
            <div className="form-group">
              <label>Title</label>
              <input
                className="form-control"
                type="text"
                name="title"
                value={values.title}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Description</label>
              <input
                className="form-control"
                type="text"
                name="description"
                value={values.description}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Price</label>
              <input
                className="form-control"
                type="number"
                name="price"
                value={values.price}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Quantity</label>
              <input
                className="form-control"
                type="number"
                name="quantity"
                value={values.quantity}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Category</label>
              <select
                className="form-control"
                name="category"
                onChange={handleChange}
              >
                <option>Please Select</option>
                {
                  values.categories.length > 0 &&
                  values.categories.map((item) => (
                    <option
                      key={item._id}
                      value={item._id}
                    >
                      {item.name}
                    </option>
                  ))
                }
              </select>
            </div>
            <FileUpload 
            loading={loading}
            setLoading={setLoading}
            values={values} 
            setValues={setValues} />
            <button className="btn btn-success mt-3 mb-3">Submit</button>
          </form>
        </div>

      </div>
    </div>
  );
};

export default CreateProduct;