import React from 'react'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import ProductTableInCart from '../card/ProductTableInCart';

// function 
import { userCart } from '../functions/users';


const Cart = () => {
  const navigate = useNavigate();
  // const dispatch = useDispatch();
  const { cart, user } = useSelector((state)=>({...state}));

  const getTotal = () => {
    return cart.reduce((currentValue,nextValue) => {
      return currentValue + nextValue.count * nextValue.price
    },0)
  }

  const handleSaveOrder = () => {
    alert('check out order')
    userCart(user.token,cart)
    .then(response=>{
      console.log(response)
      navigate('/checkout')
    }).catch(error=>{
      console.log(error)
    })

    navigate('/checkout')
  }

  const showCartItem = () => 
  <table className='table table-bordered'>
    <thead className='thead-light'>
      <tr>
        <th>Image</th>
        <th>Title</th>
        <th>Price</th>
        <th>Quantity</th>
        <th>Remove</th>
      </tr>
    </thead>
    <tbody>
      {cart.map((item,index)=>(
        <ProductTableInCart key={index} item={item} />
      ))
      }
    </tbody>
  </table>

  return (
    <div className='container-fluid'>
      <div className='row'>
        <div className='col-md-8'>
          <h4>Cart / {cart.length} product</h4>
          {!cart.length
          ? <p>No Product in cart</p>
          : showCartItem()
          }
        </div>
        <div className='col-md-4'>
          <h4>Summary</h4>
          <hr />
          {
            cart.map((item,index)=>
              <p key={index}>{item.title} x {item.count} = {item.price*item.count}</p>
            )
          }
          <hr/>
          Total : <strong>{getTotal()}</strong>
          <hr/>
          {
          user
          ? <button className='btn btn-success' disabled={!cart.length} onClick={handleSaveOrder}>Check Out</button>
          : <button className='btn btn-danger'>
              <Link 
              to='/login'
              state="cart"
              >
                Login to Checkout
              </Link>
            </button>
          }
        </div>
      </div>
    </div>
  )
}

export default Cart