import React, { useState, useEffect } from "react";
import { useSelector } from 'react-redux';
import MenubarUser from "../../layouts/MenubarUser";
import { getOrders } from "../../functions/users";
import InvoiceJsPDF from '../../order/InvoiceJsPDF';

const History = () => {

  const { user } = useSelector((state) => ({ ...state }));
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    loadData();
  }, [])

  const loadData = () => {
    getOrders(user.token)
      .then(response => {
        setOrders(response.data)
      })
  }

  // console.log(orders)

  return (
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarUser />
        </div>

        <div className="col">

          <div className="row">
            <h1>History Page</h1>

            {
              orders.map((item, index) => {
                return <div key={index}>
                  <h2>Order {' ' + item.orderstatus}</h2>

                  <table className="table">
                    <thead>
                      <tr>
                        <th>Title</th>
                        <th>Price</th>
                        <th>Count</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        item.products.map((p, i) => (
                          <tr key={i}>
                            <td>{p.product.title}</td>
                            <td>{p.price}</td>
                            <td>{p.count}</td>
                          </tr>
                        ))
                      }
                      <tr>
                        <td colSpan={3}>ราคาสุทธิ: <strong><u>{item.cartTotal}</u></strong></td>
                      </tr>
                    </tbody>
                  </table>

                  <div className="row">
                    <div className="col">
                      <InvoiceJsPDF order={item} />
                    </div>
                  </div>

                </div>
              })
            }
          </div>

        </div>

      </div>
    </div>
  );
};

export default History;
