import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import ProductCard from '../card/ProductCard';

import { Slider, Checkbox } from 'antd';

// function
import { listProduct, searchFilters } from '../functions/product';
import { listCategory } from '../functions/category';

const Shop = () => {

  const [product, setProduct] = useState([]);
  const [loading, setLoading] = useState(false);
  const [price, setPrice] = useState([0, 0]);
  const [ok, setOk] = useState(false);
  // const [disabled, setDisabled] = useState(false);

  // Category
  const [category, setCategory] = useState([]);
  const [categorySelect, setCategorySelect] = useState([]);

  const { search } = useSelector((state) => ({ ...state }));

  const { text } = search;

  // 1. Load all data
  useEffect(() => {
    loadData();
    listCategory()
      .then(response => {
        setCategory(response.data);
      })
  }, []);

  // console.log(category)

  const loadData = () => {
    setLoading(true);
    listProduct(12)
      .then(response => {
        setProduct(response.data)
        setLoading(false)
      }).catch(error => {
        setLoading(false)
        console.log(error)
      })
  }

  // 2. Load data when user filter
  useEffect(() => {
    // fetchDataFilter({ query: text })
    const delay = setTimeout(() => {
      fetchDataFilter({ query: text })
      if (!text) {
        loadData();
      }
    }, 300);
    return () => clearTimeout(delay)
  }, [text]);

  // Filter
  const fetchDataFilter = (arg) => {
    searchFilters(arg).then(response => {
      setProduct(response.data)
    })
  }

  // 3. Load on Slider
  useEffect(() => {
    fetchDataFilter({ price }) // [0,0]
  }, [ok,price]);

  const handlePrice = (value) => {
    setPrice(value);

    setTimeout(() => {
      setOk(!ok)
    }, 300)
  }

  const handleCheck = (e) => {
    // ค่าปัจจุบันที่ Check
    let inCheck = e.target.value
    // ค่าเดิมของ Check
    let inState = [...categorySelect]

    let findCheck = inState.indexOf(inCheck)

    if (findCheck === -1) {
      inState.push(inCheck);
    } else {
      inState.splice(findCheck, 1);
    }
    setCategorySelect(inState);
    fetchDataFilter({ category: inState }) // [0,0]
    if (inState.length < 1) {
      loadData()
    }

    // console.log(inState)
  }

  return (
    <div>
      <div className='container-fluid'>
        <div className='row'>
          <div className='col-md-3'>
            Filter / Search
            <hr />
            <h4>ค้นหาด้วยราคาสินค้า</h4>
            <Slider
              value={price}
              onChange={handlePrice}
              range
              max={100000}
              defaultValue={[0, 20000]}
            />

            <hr />
            <h4>ค้นหาตามหมวดหมู่สินค้า</h4>
            {category.map((item, index) =>
              <Checkbox
                key={index}
                onChange={handleCheck}
                value={item._id}
              >
                {item.name}
              </Checkbox>
            )}

          </div>
          <div className='col-md-9'>
            {
              loading
                ? <h4 className='text-danger'>Loading...</h4>
                : <h4 className='text-info'>Product</h4>
            }

            {product.length < 1 && <p>No Product Found</p>}

            <div className='row pb-5'>
              {product.map((item, index) =>
                <div key={index} className='col-md-4 mt-3'>
                  <ProductCard product={item} />
                </div>
              )
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Shop