import React from 'react'
import { jsPDF } from 'jspdf';
import { font } from './tahoma-normal';
import autoTable from 'jspdf-autotable';

const InvoiceJsPDF = ({ order }) => {

  const handlePDF = () => {

    console.log(order)

    const doc = new jsPDF();

    // add the font to jsPDF
    doc.addFileToVFS("MyFont.ttf", font);
    doc.addFont("MyFont.ttf", "MyFont", "normal");
    doc.setFont("MyFont");

    let width = doc.internal.pageSize.getWidth();

    doc.text("สวัสดีภาษาไทย", width / 2, 10, { align: 'center' });
    doc.text("วันที่", width / 2, 15, { align: 'center' });

    let data = order.products.map((item) => [
      item.product.title,
      item.price,
      item.count
    ])

    let content = {
      startY: 20,
      head: [['ชื่อสินค้า', 'ราคา', 'จำนวน']],
      body: data,
      styles: { font: 'MyFont' }
    }

    doc.autoTable(content)
    doc.text("ยอดรวมสุทธิ : " + order.cartTotal, 190, 100, { align: 'right' });
    doc.save("a4.pdf");
  }

  return (
    <div>
      <button
        onClick={handlePDF}
        className='btn btn-info'>jsPDF</button>
    </div>
  )
}

export default InvoiceJsPDF