import React, { useState, useEffect } from "react";
import MenubarAdmin from "../../../layouts/MenubarAdmin";
import { useSelector } from 'react-redux';

import { useParams, useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import FileUpload from "./FileUpload";

// functions
import { readProduct, updateProduct, } from "../../../functions/product";
import { listCategory } from "../../../functions/category";

const initialstate = {
  title: "",
  description: "",
  categories: [],
  category: "",
  price: "",
  quantity: "",
  images: []
}

const UpdateProduct = () => {

  const param = useParams();
  const navigate = useNavigate();
  const { user } = useSelector((state) => ({ ...state }));

  const [values, setValues] = useState([initialstate]);
  const [category, setCategory] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadData(user.token, param.id);
  }, [user.token, param.id])

  const loadData = () => {
    readProduct(param.id)
      .then((response) => {
        setValues({ ...values, ...response.data })
      }).catch((error) => {
        console.log(error)
      });

    listCategory(user.token)
      .then((response) => {
        // console.log(response)
        setCategory(response.data)
      }).catch((error) => {
        console.log(error)
      });
  }

  console.log('product ', values)
  console.log('category ', values)

  const handleChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value })
    // console.log(values)
  }

  const handleSubmit = (e) => {
    setLoading(true)
    e.preventDefault();
    updateProduct(user.token, values._id, values)
      .then((response) => {
        setLoading(false)
        toast.success(`Update ${response.data.title} success`)
        console.log(response)
        navigate('/admin/index')
      }).catch((error) => {
        toast.error(`Update producr error`)
        setLoading(false)
        console.log(error)
      });
  }

  return (
    <>
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarAdmin />
        </div>

        <div className="col">
          {loading
            ? <h1>Loading ...</h1>
            : <h1>Product Update</h1>
          }

          <form onSubmit={handleSubmit} className="col-md-4">
            <div className="form-group">
              <label>Title</label>
              <input
                className="form-control"
                type="text"
                name="title"
                value={values.title}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Description</label>
              <input
                className="form-control"
                type="text"
                name="description"
                value={values.description}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Price</label>
              <input
                className="form-control"
                type="number"
                name="price"
                value={values.price}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Quantity</label>
              <input
                className="form-control"
                type="number"
                name="quantity"
                value={values.quantity}
                onChange={handleChange}
              />
            </div>
            <div className="form-group">
              <label>Category</label>
              <select
                className="form-control"
                name="category"
                onChange={handleChange}
                value={values.category}
                required
              >
                <option>Please Select</option>
                {
                  category.length > 0 &&
                  category.map((item) => (
                    <option
                      key={item._id}
                      value={item._id}
                    >
                      {item.name}
                    </option>
                  ))
                }
              </select>
            </div>
            <FileUpload
              loading={loading}
              setLoading={setLoading}
              values={values}
              setValues={setValues} />
            <button className="btn btn-success mt-3 mb-3">Submit</button>
          </form>

        </div>

      </div>
    </div>
    </>
  )
}

export default UpdateProduct