import React from 'react';
import Resize from 'react-image-file-resizer';
import { useSelector } from 'react-redux';
import axios from 'axios';
import { Avatar, Badge, Space } from 'antd';

const FileUpload = ({ values, setValues, loading, setLoading }) => {
  const { user } = useSelector((state) => ({ ...state }));
  // console.log('values from FileUpload', values)
  // console.log('setValues from FileUpload',setValues)
  const handleChangeFile = (e) => {
    const files = e.target.files;
    if (files) {

      setLoading(true);

      let allfileUpload = values.images // []
      // console.log('allfileUpload in files', allfileUpload)

      for (let i = 0; i < files.length; i++) {
        Resize.imageFileResizer(
          files[i],
          720,
          720,
          "JPEG",
          100,
          0,
          (uri) => {
            // code
            axios.post(
              process.env.REACT_APP_API + '/images',
              {
                image: uri,
              },
              {
                headers: {
                  authtoken: user.token,
                },
              }
            ).then(response => {
              setLoading(false)
              allfileUpload.push(response.data)
              console.log('allfileupload in then', allfileUpload);
              setValues({ ...values, images: allfileUpload });
            }).catch(error => {
              setLoading(false)
              console.log(error)
            })
          },
          "base64"
        )

      }
    }
  }

  const handleRemove = (public_id) => {
    console.log(public_id)
    const {images} = values
    axios.post(process.env.REACT_APP_API+'/removeimages',{public_id},{
      headers:{
        authtoken:user.token
      }
    }).then(response=>{
      setLoading(false)
      let filterImages = images.filter(item=>{
        // return console.log('item',item)
        return item.public_id !== public_id
      })
      setValues({...values,images:filterImages})
    }).catch(error=>{
      console.log(error)
    })
  }

  return (
    <>
      <br/>
      {values.images && values.images.map(c=>
        <Space size={24} className='m-2' key={c.public_id}>
          <Badge 
          onClick={()=> handleRemove(c.public_id)}
          style={{cursor:'pointer'}}
          count="X"
          >
            <Avatar src={c.url} shape="square" size={120} />
          </Badge>
        </Space>
      )}

      <hr />

      <div className="form-group">
        <label>
          Choose File...
          <input
            className="form-control"
            type="file"
            multiple
            accept="images/*"
            name="file"
            onChange={handleChangeFile}
          />
        </label>
      </div>
    </>
  )
}

export default FileUpload;