import React, { useState, useEffect } from "react";
import MenubarAdmin from "../../../layouts/MenubarAdmin";

//functions
import { 
  createCategory, 
  listCategory, 
  deleteCategory 
} from "../../../functions/category";
import { Link } from "react-router-dom";
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';

//redux

const CreateCategory = () => {

  const { user} = useSelector((state) => ({...state}));

  console.log('Hello stateUser ',user.token)

  const [values, setValues] = useState({
    name: "",
  });

  const [category, setCategory] = useState([]);

  const loadData = (authtoken) => {
    listCategory(authtoken)
      .then((response) => {
        // console.log(response)
        setCategory(response.data);
      }).catch((error) => {
        console.log(error)
      })
  }

  const handleRemove = (id) => {
    deleteCategory(user.token,id)
      .then((response) => {
        console.log(response)
        loadData(user.token);
        toast.success(`Remove Data ${response.data.name} Success`)
      }).catch((error) => {
        console.log(error)
        toast.error(`Error Delete Data`)
      });
  }

  const handleChangeCategory = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value })
    // console.log(values)
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    createCategory(user.token,values)
      .then((response) => {
        console.log(response)
        loadData(user.token)
        toast.success(`Insert Data ${response.data.name} Success`)
      }).catch((error) => {
        console.log(error)
        toast.error(`Error Insert Data`)
      })
  }

  console.log(category)

  useEffect(() => {
    loadData(user.token);
  },[user.token]);

  return (
    <div className="container-fluid">
      <div className="row">

        <div className="col-md-2">
          <MenubarAdmin />
        </div>

        <div className="col">
          <h1>Create Category</h1>

          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label>เพิ่มหมวดหมู่สินค้า</label>
              <input type="text"
                name="name"
                className="form-control"
                value={values.name}
                onChange={handleChangeCategory}
              />
              <button className="btn btn-outline-primary">เพิ่มหมวดหมู่สินค้า</button>
            </div>
          </form>

          <hr />
          <ul className="list-group">
            {category.map((item, index) =>
              <li className="list-group-item" key={index}>
                {item.name} 
                <span className="badge bg-primary rounded-pill" style={{ float:"right"}} onClick={() => handleRemove(item._id)}>Delete</span> 
                <span className="badge bg-primary rounded-pill" style={{ float:"right"}}>
                  <Link to={`/admin/update-category/${item._id}`} style={{ color:"#FFF", textDecoration:"none"}}>
                    Edit
                  </Link>
                </span>
              </li>
            )}
          </ul>


        </div>

      </div>
    </div>
  )
}

export default CreateCategory